@import url(https://fonts.googleapis.com/css?family=Lato:300,400,600);

/* ---------------------------------------------------
    CSS VARIABLES
--------------------------------------------------- */
:root {
  --primary-color: #06CACB;
}

/* ---------------------------------------------------
    BASE STYLES
--------------------------------------------------- */

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

body {
  font-family: "Lato", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;

  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(90deg,
    #06CACB 0%, #06CACB 50%,
    #DDE5EE 50%, #DDE5EE 100%
  );
  background-image: linear-gradient(90deg,
    var(--primary-color) 0%, var(--primary-color) 50%,
    #DDE5EE 50%, #DDE5EE 100%
  );
  overflow-y: hidden;
}

input {
  border: none;
}
input:focus {
  outline: none;
  border-bottom: none;
}


/* FIXES LONG WORDS (LIKE URLS) BREAKING MESSAGE BUBBLES */
p {
  word-break: break-all;
}

/* FIXES WEIRD SAFARI GLITCH */
input { -webkit-user-select: text; }


@media only screen and (max-width: 800px) {
  body {
    background-image: none;
  }
}
.App {
  width: 900px;
  height: 80vh;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  display: flex;
  box-shadow: 0 0 25px rgba(0,0,0,.25);
}

.WebRTC {
  width: 900px;
  height: 700px;
  position: relative;
  background: white;
}
.WebRTC video {
  position: absolute;
  z-index: 999999;
}

/* RESPONSIVE WIDTHS */
@media only screen and (max-width: 1000px) {
  .App {
    width: 700px;
  }
}
@media only screen and (max-width: 800px) {
  .App {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    flex-direction: column;
  }
}
@media only screen and (max-width: 350px) {

}

/* RESPONSIVE HEIGHTS */
@media only screen and (max-height: 600px) {
  .App {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
  }
}
@media only screen and (min-height: 1000px) {
  .App {
    height: 800px;
  }
}
.Welcome.hidden {
  -webkit-animation: disappear 1s forwards;
          animation: disappear 1s forwards;
}
.Welcome {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: var(--primary-color);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Welcome .modal {
  width: 400px;
  padding: 60px 80px 50px 80px;
  background: #fff;
  text-align: center;
  border-radius: 5px;
  position: relative;
  box-shadow: 0 15px 35px rgba(0,0,0,.3);
  box-sizing: border-box;
}
.Welcome h2 {
  font-size: 40px;
  font-weight: 300;
  color: #888;
}
.Welcome h2 span {
  font-weight: 600;
  color: #444;
}
.Welcome .error {
  font-size: 12px;
  font-weight: 600;
  color: red;
  margin-bottom: 5px;
  display: inline-block;
}
.Welcome p {
  font-size: 13px;
}
.Welcome .info p {
  margin-top: 15px;
}
.Welcome form {
  margin-top: 15px;
}
.Welcome form input {
  width: 100%;
  font-size: 13px;
  font-weight: 300;
  color: #444;
  margin-bottom: 10px;
  padding: 10px;
  border-bottom: 1px solid rgba(127,127,127,.2);
  transition: border .25s ease-in;
}
.Welcome form input:focus {
  border-bottom: 1px solid var(--primary-color);
}
.Welcome form input.invalid {
  border-bottom: 1px solid red;
}
.Welcome form button {
  width: 100%;
  margin-top: 5px;
  font-size: 13px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 10px 20px;
  background: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
  transition: box-shadow .2s ease-in;
  cursor: pointer;
}
.Welcome form button:active,
.Welcome form button:focus {
  outline: none;
  box-shadow: 0 0 2px var(--primary-color);
}
.Welcome .redirect {
  display: inline-block;
  font-size: 12px;
  text-align: right;
  margin-top: 15px;
  font-weight: 400;
  color: var(--primary-color);
  cursor: pointer;
}
@media only screen and (max-width: 400px) {
  .Welcome .modal {
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .Welcome form input {
    width: 100%;
  }
}
.Sidebar {
  box-sizing: border-box;
  width: 250px;
  padding: 20px 40px;;
  background: #fff;
  box-shadow: 4px 0 4px rgba(0,0,0,.1);
  /* z-index: 1; */
  position: relative;
  overflow: auto;
}
.Sidebar .heading {
  margin: 20px 0;
}
.Sidebar .heading h2 {
  font-size: 32px;
  font-weight: 300;
  text-align: center;
  color: #888;
}
.Sidebar .heading h2 span {
  font-weight: 600;
  color: #444;
}
.Sidebar .heading .subtitle {
  margin-top: 0;
  text-align: center;
  font-size: 12px;
  color: #444;
}

.sidebar-items {
  overflow: auto;
}

.Sidebar .RoomList,
.Sidebar .UserList {
  margin-top: 10px;
  overflow-y: auto;
}
.Sidebar .RoomList .rooms,
.Sidebar .UserList .users {
  max-height: 300px;
}
.Sidebar .RoomList h3,
.Sidebar .UserList h3 {
  font-weight: 400;
  font-size: 12px;
  text-transform: uppercase;
  color: #888;
}
.Sidebar .RoomList .Room,
.Sidebar .UserList .User {
  margin: 7px 0;
  padding-left: 15px;
  font-size: 12px;
  font-weight: 600;
  color: #888;
  position: relative;
}

.Sidebar .mobile-button {
  display: none;
  height: 35px;
  width: 35px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border: 1px solid rgba(0,0,0,.2);
  border-radius: 5px;
  box-shadow: 0 2px 2px rgba(64,64,64,.15);
  transition: box-shadow .25s ease-in;
  cursor: pointer;
}
.Sidebar .mobile-button:hover {
  box-shadow: none;
}
.Sidebar #sidebar-toggle {
  display: none;
}
.Sidebar .mobile-button .lines {
  width: 50%;
  height: 1px;
  background: #777;
  position: absolute;
  transition: all .2s;
}
.Sidebar .mobile-button .lines:nth-child(1) {
  left: 50%;
  top: 33%;
  -webkit-transform: translate(-50%, -33%);
          transform: translate(-50%, -33%);
}
.Sidebar .mobile-button .lines:nth-child(2) {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.Sidebar .mobile-button .lines:nth-child(3) {
  left: 50%;
  top: 67%;
  -webkit-transform: translate(-50%, -67%);
          transform: translate(-50%, -67%);
}

.Sidebar #sidebar-toggle:checked ~ .mobile-button .lines:nth-child(1) {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(135deg);
          transform: translate(-50%, -50%) rotate(135deg);
}
.Sidebar #sidebar-toggle:checked ~ .mobile-button .lines:nth-child(2) {
  display: none;
}
.Sidebar #sidebar-toggle:checked ~ .mobile-button .lines:nth-child(3) {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-135deg);
          transform: translate(-50%, -50%) rotate(-135deg);
}
/* RESPONSIVE WIDTHS */
@media only screen and (max-width: 1000px) {
  .Sidebar .heading {
    margin: 10px 0;
  }
}
@media only screen and (max-width: 800px) {
  .Sidebar {
    /* display: none; */
    width: 100%;
    height: 70px;
    margin: 0;
    padding: 10px 30px;
    box-shadow: 0 4px 4px rgba(0,0,0,.1);
  }
  .Sidebar .mobile-button {
    display: block;
  }
  .Sidebar .heading {
    margin: 0;
  }
  .Sidebar .heading h2 {
    font-size: 28px;
  }
  .Sidebar .heading .subtitle {
    font-size: 10px;
  }
  .sidebar-items {
    position: fixed;
    top: 70px;
    left: 0;
    bottom: 0;
    padding: 15px;
    margin-left: -250px;
    width: 200px;
    background: #fff;
    box-shadow: 4px 0 4px rgba(0,0,0,.1);
    transition: margin .5s ease-in-out;
  }
  .Sidebar #sidebar-toggle:checked ~ .sidebar-items {
    margin-left: 0;
  }
  /* .Sidebar .RoomList,
  .Sidebar .UserList {
    margin-top: 5px;
  } */
}
@media only screen and (max-width: 450px) {
  .Sidebar {
    height: 50px;
    padding: 5px 20px;
  }
  .Sidebar .mobile-button {
    height: 30px;
    width: 30px;
  }
  .Sidebar .heading h2 {
    text-align: right;
  }
  .Sidebar .heading .subtitle {
    display: none;
  }
  .Sidebar .sidebar-items {
    top: 50px;
  }
}

/* RESPONSIVE HEIGHTS */
@media only screen and (max-height: 600px) {
  
}

.Messages {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
.MessageList {
  padding: 15px 50px;
  flex: 1 1;
  overflow-y: auto;
  background: #DFE5EE;
  display: flex;
  flex-direction: column;
}
.Message {
  margin: 7px 0;
  max-width: 65%;
}
.Message .bubble {
  padding: 7px 12px;
  border-radius: 5px;
  color: #555;
  font-weight: 400;
  font-size: 14px;
  background: #fff;
  box-shadow: 0 4px 7px rgba(0,0,0,.2);
  transition: box-shadow .2s ease-in;
  position: relative;
  display: inline-block;
}
.Message .bubble:hover {
  box-shadow: 0 2px 3px rgba(0,0,0,.2);
}
.Message .bubble::before {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  left: -9px;
  top: 0;
  border: 9px solid black;
  border-top-color: #fff;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.Message .bubble p {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
}
.Message .meta {
  display: flex;
  font-size: 9px;
  margin-top: 2px;
}
.Message .name {
  color: var(--primary-color);
  font-weight: 600;
  text-transform: uppercase;
  display: inline-block;
}
.Message .date {
  font-weight: 400;
  color: #888;
  margin-left: auto;
  padding-left: 15px;
}

.Message.me {
  margin-left: auto;
}
.Message.me .bubble {
  background: #06C9CB;
  color: #fff;
}
.Message.me .bubble::before {
  left: calc(100% - 9px);
  border-top-color: #06C9CB;
}
.Message.me .name {
  display: none;
}
.Message.me .date {
  color: #fff;
}

.CreateMessage {
  box-sizing: border-box;
  height: 60px;
}
.CreateMessage form {
  height: 100%;
  width: 100%;
  background: #efefef;
  padding: 10px 25px;
}
.CreateMessage input {
  padding: 10px 20px;
  height: 100%;
  width: 100%;
  font-size: 15px;
  border-radius: 25px;
  background: #fff;
  color: #444;
}
.CreateMessage input::-webkit-input-placeholder {
  color: rgba(0,0,0,.3);
}
.CreateMessage input::-ms-input-placeholder {
  color: rgba(0,0,0,.3);
}
.CreateMessage input::placeholder {
  color: rgba(0,0,0,.3);
}

